.Tile{
    -webkit-box-shadow: 0 4px 8px 0 rgba(53, 144, 230, 0.2);
            box-shadow: 0 4px 8px 0 rgba(53, 144, 230, 0.2);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    border-radius: 5px;
    width: 300px;
    display: inline-block;
    background-color: blanchedalmond;
    color: #091f3f;
    margin: 8px;
    padding: 5px;
}

.TileTop{
    display: -ms-flexbox;
    display: flex;
    border-bottom: 2px solid #40a4c8;
}

.TileBottom {
    margin: 8px;
}

.TileTop h3 {
    margin-left: 7px;
    text-transform: uppercase;
}

.Horseimg {
    padding: 3px;
    height: 50px;
}


