.NavItem {
    margin: 10px 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
}

.NavItem a{
    color: #8f5c2c;
    text-decoration: none;
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
}

.NavItem a:hover,
.NavItem a:active,
.NavItem a.active {
    color: #40a4c8;
}

@media(min-width: 500px){
    .NavItem {
        margin: 0;
        display: -ms-flexbox;
        display: flex;
        height: 100%;
        width: auto;
        -ms-flex-align: center;
            align-items: center;
    }
    
    .NavItem a{
        color: white;
        height: 100%;
        padding: 16px 10px;
        border-bottom: 4px solid transparent;
    }
    
    .NavItem a:hover,
    .NavItem a:active,
    .NavItem a.active {
        background-color: #8f5c2c;
        border-bottom: 4px solid #091f3f;
        border-top: 4px solid #091f3f;
        color: white;
    }
}


