.Toolbar{
    height: 56px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    border-bottom: 4px solid #40a4c8;
    background-color: #996738;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    z-index: 90;
}

.Toolbar nav{
    height: 100%;
}

.Logo{
    height: 80%;
    margin-right: 20px;
}

.Logo img{
    height: 100%;
}


